@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.results{display:flex}.results h2{margin:.5em 0 1em}.results .disclaimer{margin:2em 0;font-size:1.2em}.results .office-result{width:33%;margin:0 1em;text-align:center}.results .current-location{opacity:.7}.results .office-results{display:flex}.results .day{border-right:1px solid #eaeaea;width:20%;padding:0 1em}.results .day:last-child{border-right:0}.results .day .weather-icon{width:32px;height:32px;margin:.5em 0 0}.results .day .date{font-size:.8em;color:#999}.results .day .temp-forecast{margin:.5em 0}.results .flights .flight-info{border-bottom:1px dotted #ccc}.results .flights .flight-info:first-child{font-weight:bold}.results .flights .flight-info:last-child{border-bottom:0}.results .flights .flight-info .flight-info-detail{margin:.6em 0}.results .flights .flight-info em{color:#989898;display:block;text-transform:uppercase;font-size:.5em;font-style:normal}.results .flights .book-flight-link{text-decoration:none;color:#004dff}

.preferences{margin:1em;display:flex;justify-content:center}.preferences input[type="number"]{width:2em;margin:0 1em}.main-msg{height:96vh;display:flex;align-items:center;justify-content:center}

